html {
  font-size: 62.5%;
}

.pagevisual {
  height: 250px;
  width: 100%;
  background: url(../../../img/mv.png) center/cover;
  display: flex;
  align-items: center;
  justify-content: center;
}

.pagevisual-ttl {
  color: white;
}

.blog-inner {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  margin-top: 20px;
}

.blog-item {
  width: 32%;
}

.blog-item:nth-of-type(n+2) {
  margin-left: 2%;
}

@media screen and (max-width: 768px) {
  .blog-item {
    margin-left: 0px;
    width: 100%;
  }
  .blog-item:nth-of-type(n+2) {
    margin-top: 20px;
  }
}

.blog-item a:hover {
  opacity: 0.7;
  transition: 0.3s;
}

.blog-item img {
  width: 100%;
}

.blog-text {
  margin-top: 10px;
}

.blog-date {
  font-size: 1.4rem;
}

@media screen and (max-width: 768px) {
  .blog-date {
    text-align: center;
  }
}

@media screen and (max-width: 768px) {
  .blog-heading {
    text-align: center;
  }
}

.blog-heading:hover {
  opacity: 0.7;
  transition: 0.3s;
}

.blog-btn {
  display: inline-block;
  width: 300px;
  max-width: 300px;
  font-size: 1.6rem;
  color: white;
  font-weight: bold;
  padding: 10px;
  background-color: #cd0224;
  text-align: center;
}

.blog-btn:hover {
  opacity: 0.7;
  transition: 0.3s;
}

.blog-item {
  margin-top: 20px;
}

.blog-item:nth-of-type(4) {
  margin-left: 0px;
}
