.blog{
  &-inner{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    margin-top: 20px;
  }
  &-item{
    width: 32%;
    &:nth-of-type(n+2){
      margin-left: 2%;
    }
    @include mq{
      margin-left: 0px;
      width: 100%;
      &:nth-of-type(n+2){
        margin-top: 20px;
    }
    }
    a{
      &:hover{
        @include _hover;
      }
    }
    img{
      width: 100%;
    }
  }
  &-text{
    margin-top: 10px;
   
  }
  &-date{
    @include fz(14);
    @include mq{
      text-align: center;
    }
  }
  &-heading{
    @include mq{
      text-align: center;
    }
    &:hover{
      @include _hover;
    }
  }
  &-btn{
    @include _btn;
    &:hover{
      @include _hover;
    }
  }
}